/* width */
::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}
/* Track */
::-webkit-scrollbar-track {
    background: #121615;
}
/* Handle */
::-webkit-scrollbar-thumb {
    background: #0b3832;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #0a2320;
}

body {
    --theme-blue: #05fdd8;
    --theme-red: #ff5e84;
    --theme-background: #132a27;

    background: #1d1d1d;
    margin: 0;
    font-family: "Roboto", Sans-serif;
}

.d-none {
    display: none;
}
.d-flex {
    display: flex;
}
.d-flex-col {
    display: flex;
    flex-direction: column;
}

a {
    text-decoration: none;
}
